.input-labels[data-v-1b2fc7cc]{
  cursor :pointer; 
  position: absolute; 
  width :.39rem;
  height:.39rem;
  margin: auto;
  background: #939393; 
  border-radius: 50%;
}
.input-labels[data-v-1b2fc7cc]:hover
  {background: #c5161d;
}
.input-labels[data-v-1b2fc7cc]:after{
  opacity: 0;
  content: '';
  position: absolute;
  width: 9px;
  height: 5px;
  background: transparent;
  top :6px;
  left: 6px;
  border: 2px solid #333;
  border-top :none;
  border-right: none;
  -webkit-transform: rotate(-45deg) ;
  transform :rotate(-45deg) ;
}
