
ul,li{
    padding: 0;
    margin: 0;
}
.index-list ul li{
    height: 2.3rem;
}
.index-list ul li img{
    width: 100%;
    height: 100%;
}
a{
    text-decoration:none;
}
body{
    background: none !important;
}
#app{
    width: 100%;
    height: 100%;
    background: #FAFAFA;
}
#app .logo{
    height: 1.3rem;
}
#app .logo .logo-left{
    position: relative;
}
#app .logo .logo-left img{
    margin-top:.2rem;
    height: .9rem;
    width: 1.3rem;
}
#app .logo .logo-right{
    float: right;
}
#app .logo .logo-right img{
    width: .5rem;
    position: absolute;
    top: .35rem;
    right: .3rem;
}
#app .mint-swipe{
    height: 3.58rem !important;
}
#app .mint-swipe .mint-swipe-items-wrap .banner{
    width: 7.5rem;
}
#app .mint-swipe .mint-swipe-items-wrap .banner img{
    height: 100%;
}
.mint-swipe-indicator{
    width: .18rem !important;
    height: .18rem !important;
    opacity:1 !important;
    background:none !important;
    border: 1px solid #ffffff;
}
.is-active{
    border: none !important;
    background: #C4171D !important;
}
.mint-swipe-indicators{
    bottom: .5rem !important;
}
#app .hs{
    height:.33rem;
    background:#FAFAFA;
    position: relative;
}
#app .hs .search{
    width:4.65rem;
    height:.7rem;
    border:1px solid #E3E3E3;
    position:absolute;
    top:-.5rem;
    left:20%;
    border-top-left-radius:.1rem;
    border-top-right-radius: .1rem;
    border-bottom-left-radius: .1rem;
    border-bottom-right-radius: .1rem;
}
#app .hs .search input{
    width: 100%;
    height: 100%;
    border-top-left-radius:.1rem;
    border-top-right-radius: .1rem;
    border-bottom-left-radius: .1rem;
    border-bottom-right-radius: .1rem;
    border: none;
    font-size: .24rem;
    font-family: "SourceHanSerifSC-Light";
    position: relative;
}
#app .hs .search img{
    width: .31rem;
    height: .31rem;
    position: absolute;
    top: .19rem;
    right: .28rem;
}
#app .mint-swipe{
    height: 200px;
}
.banner>img{
    width: 100%;
    height: 200px;
}
#app .mui-table-view{
    margin-top: 50px;
}
#app .mui-table-view.mui-grid-view.mui-grid-9 .mui-table-view-cell{
    border:0;
}
#app .mui-table-view.mui-grid-view.mui-grid-9 img{
    width: 60px;
    height:60px;
}
#app .banner{
    width: 100%;
    margin-top: 0;
    padding-top: 0;
}
#app .banner img{
    width: 100%;
}
.list .goods-item .left img{
   width: 100%;
}
.banner img{
    width: 100%;
}
.index-list{
    margin-bottom: 1.3rem;
}



